<template>
    <div class="card">
        <div class="header">
            <img
                src="@/assets/utilitya.svg"
                style="width: 24px; height: 24px"
            />
            <h2 class="top_info_heading">Utility Chain</h2>
            <div class="headera">
                <transition name="blink" mode="out-in">
                    <span v-if="showIncomingText" class="subtexter"
                        >Incoming Transaction</span
                    >
                    <span v-else class="subtext">Recent Activity</span>
                </transition>
            </div>
        </div>
<table>
  <thead>
     <tr class="ori">
      <th style="font-size: 10px; text-transform: uppercase; letter-spacing: 0.5px;">Hash</th>
      <th style="font-size: 10px; text-transform: uppercase; letter-spacing: 0.5px;">Amount</th>
      <th style="font-size: 10px; text-transform: uppercase; letter-spacing: 0.5px;">Timestamp</th>
    </tr>
  </thead>

  <!-- transition-group for per-row animation -->
  <transition-group
    name="tx-list"
    tag="tbody"
  >
    <tr
      v-for="(transaction, index) in Transactions"
      :key="transaction.hash"
      class="tx-row"
      :style="{ animationDelay: (index * 100) + 'ms' }"
    >
      <td>{{ transaction.hash }}</td>
      <td>
        {{ formatValue(transaction.value) }} DJT
        <img
          src="@/assets/zuli.svg"
          style="width: 16px; height: 16px; margin-left: 4px"
        />
      </td>
      <td>{{ transaction.createdAt | fromNow }}</td>
    </tr>
  </transition-group>
</table>
    </div>
</template>
<script>
export default {
  data() {
    return {
      Transactions: [],
      showIncomingText: false,
      previousLatestTransactionId: null,
      refreshInterval: null,
      mockInterval: null,
      maxTransactions: 15, // keep only last N
    }
  },
  created() {
    this.fetchData()
    this.refreshInterval = setInterval(this.refreshData, 5000)
    this.startMocking()
  },
  beforeDestroy() {
    clearInterval(this.refreshInterval)
    clearInterval(this.mockInterval)
  },
  methods: {
    async fetchData() {
      try {
        const response = await fetch(
          'https://djtexplorer.uksouth.cloudapp.azure.com/v2/ctransactions?limit=8&sort=timestamp-desc'
        )
        if (response.ok) {
          const data = await response.json()
          const apiTxns = data.Transactions || []

          // Filter: only keep ones not already in our list
          const newOnes = apiTxns.filter(
            (tx) => !this.Transactions.find((t) => t.hash === tx.hash)
          )

          if (newOnes.length > 0) {
            this.showIncomingText = true
            setTimeout(() => {
              this.showIncomingText = false
            }, 2000)

            this.Transactions = [...newOnes, ...this.Transactions]
          }

          // Trim to max length
          if (this.Transactions.length > this.maxTransactions) {
            this.Transactions = this.Transactions.slice(0, this.maxTransactions)
          }

          this.previousLatestTransactionId = apiTxns[0]?.hash || null
        } else {
          console.error('API request failed:', response.statusText)
        }
      } catch (error) {
        console.error('An error occurred:', error)
      }
    },
    refreshData() {
      this.fetchData()
    },
    startMocking() {
      this.mockInterval = setInterval(() => {
        const batchSize = Math.random() < 0.3 ? 2 + Math.floor(Math.random() * 2) : 1
        const newTxns = []

        for (let i = 0; i < batchSize; i++) {
          newTxns.push({
            hash: '0x' + Math.random().toString(16).substr(2, 10),
            value: Math.floor(Math.random() * 5 * 1e18), // up to 5 DJT
            createdAt: new Date().toISOString(),
            mock: true,
          })
        }

        this.Transactions = [...newTxns, ...this.Transactions]

        if (this.Transactions.length > this.maxTransactions) {
          this.Transactions = this.Transactions.slice(0, this.maxTransactions)
        }

        this.showIncomingText = true
        setTimeout(() => {
          this.showIncomingText = false
        }, 1500)
      }, 4000 + Math.random() * 3000)
    },
    formatValue(value) {
      const etherValue = value / Math.pow(10, 18)
      return etherValue.toLocaleString(undefined, {
        minimumFractionDigits: 2,
        maximumFractionDigits: 2,
      })
    },
  },
}
</script>

