


































import { Vue, Component } from 'vue-property-decorator'
import Tooltip from '@/components/rows/Tooltip.vue'
import TxTableHead from '@/components/rows/TxRow/TxTableHead.vue'
import TxRow from '@/components/rows/TxRow/TxRow.vue'
import { ITransaction } from '@/store/modules/transactions/models'
import RecentTxHeader from '@/components/Transaction/RecentTxHeader.vue'
import { makeMockTransaction } from '@/mocks/transaction'

@Component({
    components: {
        Tooltip,
        TxTableHead,
        TxRow,
        RecentTxHeader,
    },
})
export default class RecentTransactions extends Vue {
    loading = false
    poller = 0
    limit = 25
    sort = 'timestamp-desc'

    // Local mock transactions
    mockTransactions: ITransaction[] = []
    mockInterval: number | null = null

    created() {
        this.poller = window.setInterval(() => this.pollForTxUpdates(), 5000)
        window.addEventListener(
            'TRANSACTION_POLLING:KILL',
            this.killPollingInterval.bind(this)
        )
    }

    mounted() {
        this.startMockStream()
    }

    destroyed() {
        this.killPollingInterval()
        if (this.mockInterval) clearInterval(this.mockInterval)
        window.removeEventListener(
            'TRANSACTION_POLLING:KILL',
            this.killPollingInterval.bind(this)
        )
    }

    private killPollingInterval() {
        window.clearInterval(this.poller)
    }

    get assetsLoaded(): boolean {
        return this.$store.state.assetsLoaded
    }

    // Real transactions from Vuex store, always return an array
    get transactions(): ITransaction[] {
        const txs = this.$store.state.recentTxRes.transactions
        return Array.isArray(txs) ? txs : []
    }

    // Merge real + mock, descending timestamp order
    get allTransactions(): ITransaction[] {
        const combined = [...this.transactions, ...this.mockTransactions]
        return combined.sort((a, b) => {
            const tA = new Date(a.timestamp).getTime()
            const tB = new Date(b.timestamp).getTime()
            return tB - tA
        })
    }

    pollForTxUpdates(): void {
        this.$store.dispatch('getRecentTransactions', {
            id: null,
            params: {
                sort: this.sort,
                limit: this.limit,
            },
        })
    }

    // Stream fake transactions every few seconds with variable count and staggered appearance
    startMockStream() {
        // Initial batch of mocks
        this.mockTransactions = Array.from({ length: 4 }).map(() =>
            makeMockTransaction()
        )

        this.mockInterval = window.setInterval(() => {
            // Random batch size 13
            const count = Math.floor(Math.random() * 5) + 1
            const newTxs = Array.from({ length: count }).map(() =>
                makeMockTransaction()
            )

            // Staggered insertion
            newTxs.forEach((tx, index) => {
                setTimeout(() => {
                    this.mockTransactions.unshift(tx)
                    // Keep only latest 50 mock transactions
                    if (this.mockTransactions.length > 50) {
                        this.mockTransactions.pop()
                    }
                }, index * (100 + Math.random() * 200)) // 100300ms stagger
            })
        }, 5000) // every 5 seconds
    }
}
