import { AxiosResponse } from 'axios'
import Big from 'big.js'
import { ITransaction, Input, Output } from '@/store/modules/transactions/models'

export function resolveResponseData<T = any>(res: AxiosResponse<T>) {
    return res.data
}

export function makeMockTransaction(id: string): ITransaction {
    // Minimal Input/Output mocks
    const mockInput: Input = {
      credentials: {
        signature: '',
        public_key: '',
        address: `X-mock-from-${id}`,
      },
      output: {
        id: `out-${id}-0`,
        transactionID: id,
        redeemingTransactionID: '',
        outputIndex: 0,
        chainID: '11111111111111111111111111111111LpoYY',
        assetID: 'mock-asset',
        timestamp: new Date(),
        amount: new Big(Math.floor(Math.random() * 1e6)),
  
        outputType: 0,
        groupID: 0,
  
        // staking-related
        stake: false,
        stakeableout: false,
        stakeLocktime: 0,
        rewardUtxo: false,
  
        // x-chain flags
        genesisutxo: false,
        frozen: false,
        locktime: 0,
        threshold: 1,
        payload: null,
  
        // addresses
        addresses: [`X-mock-from-${id}`],
        caddresses: [],
  
        // c-chain
        block: '',
        nonce: 0,
      },
    }
  
    const mockOutput: Output = {
      id: `out-${id}-1`,
      transactionID: id,
      redeemingTransactionID: '',
      outputIndex: 0,
      chainID: '11111111111111111111111111111111LpoYY',
      assetID: 'mock-asset',
      timestamp: new Date(),
      amount: new Big(Math.floor(Math.random() * 1e6)),
  
      outputType: 0,
      groupID: 0,
  
      stake: false,
      stakeableout: false,
      stakeLocktime: 0,
      rewardUtxo: false,
  
      genesisutxo: false,
      frozen: false,
      locktime: 0,
      threshold: 1,
      payload: null,
  
      addresses: [`X-mock-to-${id}`],
      caddresses: [],
      block: '',
      nonce: 0,
    }
  
    return {
      id,
      chainID: '11111111111111111111111111111111LpoYY',
      type: 'transfer',
  
      inputs: [mockInput],
      outputs: [mockOutput],
  
      memo: '',
      inputTotals: {},
      outputTotals: {},
      reusedAddressTotals: null,
  
      timestamp: new Date().toISOString(),
      txFee: 0,
      genesis: false,
      rewarded: false,
      rewardedTime: null,
      epoch: 0,
      vertexId: '',
      validatorNodeID: '',
      validatorStart: 0,
      validatorEnd: 0,
      txBlockId: '',
    }
  }
