
































import 'reflect-metadata'
import { Vue, Component, Prop, Ref, Watch } from 'vue-property-decorator'
import { trimmedLocaleStrings, backgroundColor } from '@/helper'
import AddressLink from '@/components/rows/TxRow/AddressLink.vue'
import UtxosBreakdownModal from '@/components/modals/UtxosBreakdown/UtxosBreakdownModal.vue'
import { Asset } from '@/js/Asset'
import Big from 'big.js'
import { DisplayAddress } from '@/js/Transaction'

@Component({
    components: {
        AddressLink,
    },
})
export default class OutputUtxo extends Vue {
    @Prop() output!: any

    get asset(): Asset {
        if (this.$store.state.assets[this.output.assetID]) {
            return this.$store.state.assets[this.output.assetID]
        }
        this.$store.dispatch('addUnknownAsset', this.output.assetID)
        //@ts-ignore
        return {
            id: '',
            alias: '',
            chainID: '',
            currentSupply: Big(0),
            denomination: 0,
            name: '',
            symbol: '',
            profane: false,
        }
    }

    get addresses(): DisplayAddress[] {
        return this.output.addresses
    }

    get amount(): string {
        const amt = Big(this.output.amount)
        return trimmedLocaleStrings(amt, this.asset.denomination)
    }

get leftover(): string {
  const [_, decimals = ''] = this.amount.split('.')

  // Always take exactly 2 decimal digits
  const fixedDecimals = decimals.padEnd(2, '0').slice(0, 2)

  return fixedDecimals ? '.' + fixedDecimals : '.00'
}

    get isNFT(): boolean {
        switch (this.output.outputType) {
            case 7:
                return false
            case 10:
                return true
            case 11:
                return true
            default:
                return false
        }
    }

    get background(): string {
        return backgroundColor(this.addresses[0].displayAddress!)
    }
}
