  mounted() {
    this.addInitialLogs()
    this.startLogging()
  },
  
  beforeDestroy() {
    if (this.logInterval) clearInterval(this.logInterval as NodeJS.Timeout)
  },
  
  methods: {
    addRandomLog(): void {
      const randomMessage = this.sampleMessages[Math.floor(Math.random() * this.sampleMessages.length)]
      const randomMessages = this.sampleMessagesTwo[Math.floor(Math.random() * this.sampleMessages.length)]
      const randomType = this.sampleTypes[Math.floor(Math.random() * this.sampleTypes.length)]
      const randomChain = this.sampleChains[Math.floor(Math.random() * this.sampleChains.length)]
      const randomHash = '0x' + Math.floor(Math.random() * 2 ** 64).toString(16).padStart(16, '0')
      
      this.logs.unshift({
        id: this.nextId++,
        type: randomType,
        message: randomMessage,
        messages: randomMessages,
        timestamp: new Date(),
        txhash: randomHash,
        chain: randomChain,
      })
      
      if (this.logs.length > 15) this.logs.pop()
    },
    
    addInitialLogs(): void {
      for (let i = 0; i < 5; i++) {
        setTimeout(() => this.addRandomLog(), i * 300)
      }
    },
    
    startLogging(): void {
      if (this.logInterval) clearInterval(this.logInterval)
      const addLogs = () => {
        const logsToAdd = Math.random() < 0.2 ? Math.floor(Math.random() * 3) + 1 : 1
        for (let i = 0; i < logsToAdd; i++) {
          setTimeout(() => this.addRandomLog(), i * Math.random() * 300)
        }
        const nextDelay = Math.random() < 0.1 
          ? 200 + Math.random() * 800
          : 1000 + Math.random() * 4000
        this.logInterval = setTimeout(addLogs, nextDelay)
      }
      addLogs()
    },
    
    formatTime(timestamp: Date): string {
      return timestamp.toLocaleTimeString('en-US', {
        hour12: false,
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit'
      })
    },
    
    onEnter(el: HTMLElement): void {
      el.classList.add('log-entering')
      setTimeout(() => el.classList.remove('log-entering'), 600)
    },
    
    onLeave(el: HTMLElement): void {
      el.classList.add('log-leaving')
    }
  }
})
