<template>
    <div class="card">
        <div class="header">
            <img src="@/assets/methoda.svg" style="width: 24px; height: 24px" />
            <h2 class="top_info_heading">Method Chain</h2>
            <div class="headera">
                <transition name="blink" mode="out-in">
                    <span v-if="showIncomingText" class="subtexter"
                        >Incoming Transaction</span
                    >
                    <span v-else class="subtext">Recent Activity</span>
                </transition>
            </div>
        </div>
        <table>
            <thead>
                <tr class="ori">
                    <th style="font-size: 10px; text-transform: uppercase; letter-spacing: 0.5px;">Tx ID</th>
                    <th style="font-size: 10px; text-transform: uppercase; letter-spacing: 0.5px;">Type</th>
                    <th style="font-size: 10px; text-transform: uppercase; letter-spacing: 0.5px;">Timestamp</th>
                </tr>
            </thead>
            <transition-group name="tx-list" tag="tbody">
                <tr v-for="(transaction, index) in transactionDisplayInfo"
                    :key="transaction.id"
                    :style="{ animationDelay: (index * 100) + 'ms' }"
                    class="tx-row"
                >
                    <td>
                        <router-link :to="`/tx/${transaction.id}`" class="jan"
                            >{{ transaction.id }}...</router-link
                        >
                    </td>
                    <td>
                        {{ transaction.displayType }}
      <img
        src="@/assets/validator-icon.svg"
        style="width: 16px; height: 16px; margin-left: 4px"
      />
                    </td>
                    <td>{{ transaction.timestamp | fromNow }}</td>
                </tr>
                </transition-group>
        </table>
    </div>
</template>
<script>
export default {
    data() {
        return {
            transactions: [],
            showIncomingText: false,
            previousLatestTransactionId: null,
                  refreshInterval: null,
      mockInterval: null,
      maxTransactions: 15,
        }
    },
    computed: {
        transactionDisplayInfo() {
            const typeMap = {
                add_delegator: {
                    display: 'Delegation',
                },
                pvm_import: {
                    display: 'Import',
                },
                add_validator: {
                    display: 'Validator',
                },
                create_subnet: {
                    display: 'ECC',
                },
            }

            return this.transactions.map((transaction) => {
                const { display } = typeMap[transaction.type] || {
                    display: transaction.type,
                }
                return {
                    ...transaction,
                    displayType: display,
                }
            })
        },
    },
    created() {
        this.fetchData()
        this.refreshInterval = setInterval(this.refreshData, 5000) // Refresh every 5 seconds
        this.startMocking()
    },
    beforeDestroy() {
        clearInterval(this.refreshInterval)
        clearInterval(this.mockInterval)
    },
    methods: {
        async fetchData() {
            try {
                const response = await fetch(
                    'https://djtexplorer.uksouth.cloudapp.azure.com/v2/transactions?chainID=11111111111111111111111111111111LpoYY&sort=timestamp-desc&limit=8'
                )
        if (response.ok) {
          const data = await response.json()
          const apiTxns = data.transactions || []

          const newOnes = apiTxns.filter(
            (tx) => !this.transactions.find((t) => t.id === tx.id)
          )

          if (newOnes.length > 0) {
            this.showIncomingText = true
            setTimeout(() => (this.showIncomingText = false), 2000)

            this.transactions = [...newOnes, ...this.transactions]
          }

          if (this.transactions.length > this.maxTransactions) {
            this.transactions = this.transactions.slice(0, this.maxTransactions)
          }

          this.previousLatestTransactionId = apiTxns[0]?.id || null
        } else {
          console.error('API request failed:', response.statusText)
        }
      } catch (error) {
        console.error('An error occurred:', error)
      }
        },
        formatDate(date) {
            return new Date(date).toLocaleString()
        },
        formatValue(value) {
            const etherValue = value / Math.pow(10, 18)
            return etherValue.toLocaleString(undefined, {
                minimumFractionDigits: 2,
                maximumFractionDigits: 2,
            })
        },
        refreshData() {
            this.fetchData()
        },
            startMocking() {
      this.mockInterval = setInterval(() => {
        const batchSize = Math.random() < 0.3 ? 2 + Math.floor(Math.random() * 2) : 1
        const newTxns = []

        for (let i = 0; i < batchSize; i++) {
          newTxns.push({
            id: Math.random().toString(16).substr(2, 10),
            type: ['Delegation', 'Validator', 'Import', 'ECC'][Math.floor(Math.random() * 4)],
            timestamp: new Date().toISOString(),
            mock: true,
          })
        }

        this.transactions = [...newTxns, ...this.transactions]

        if (this.transactions.length > this.maxTransactions) {
          this.transactions = this.transactions.slice(0, this.maxTransactions)
        }

        this.showIncomingText = true
        setTimeout(() => (this.showIncomingText = false), 1500)
      }, 4000 + Math.random() * 3000)
    },
    },
}
</script>

