// Generate a mock timestamp: within the last 5 seconds
function generateRecentTimestamp(): string {
    const now = Date.now()
    const randomOffset = Math.floor(Math.random() * 500)
    return new Date(now - randomOffset).toISOString()
}

function randomTxType(): string {
    const keys = Array.from(txTypeMap.keys())
    return randomChoice(keys)
  }

  function generateAmount(): string {
    const min = 1e9      // 1 DJTX
    const max = 1e13     // 10,000 DJTX
    return Math.floor(Math.random() * (max - min) + min).toString()
  }

// Generate a realistic mock transaction ID
function generateMockTxID(): string {
    // Avalanche transaction IDs are base58-like, 43-44 chars
    return nanoid(43)
}

export function generateMockTx() {
    const txId = generateTxId()
    const type = randomTxType()
  
    // random 13 inputs and 13 outputs
    const inputCount = Math.floor(Math.random() * 3) + 1
    const outputCount = Math.floor(Math.random() * 3) + 1
  
    return {
      id: txId,
      chainID: 'V', // or randomize between X, P, C
      type,
      timestamp: new Date().toISOString(),
      inputs: generateInputs(inputCount),
      outputs: generateOutputs(outputCount),
      memo: '',
      inputTotals: {},
      outputTotals: {},
      reusedAddressTotals: null,
      txFee: 1000000, // 0.001 DJTX
      genesis: false,
      rewarded: false,
      rewardedTime: null,
      epoch: 0,
      vertexId: '',
      validatorNodeID: '',
      validatorStart: 0,
      validatorEnd: 0,
      txBlockId: '',
    }
  }

