// Avalanche-style tx ID generator (43 chars, alphanumeric)
const alphabet = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789'
const generateTxId = customAlphabet(alphabet, 43)

// Address generator with "V-dijets1" prefix
const addrAlphabet = 'abcdefghijklmnopqrstuvwxyz0123456789'
const generateAddress = customAlphabet(addrAlphabet, 38) 

// Utility: pick random item
function randomChoice<T>(arr: T[]): T {
  return arr[Math.floor(Math.random() * arr.length)]
}

// Generate realistic amounts in nDJTX (nano DJTX)

// Generate random input set
function generateInputs(count: number) {
  return Array.from({ length: count }).map(() => ({
    output: {
      addresses: [{ address: `V-dijets1${generateAddress()}` }],
    },
  }))
}

// Generate random output set
function generateOutputs(count: number) {
  return Array.from({ length: count }).map(() => ({
    addresses: [{ address: `V-dijets1${generateAddress()}` }],
    amount: generateAmount(),
  }))
}

