




import { Vue, Component, Prop } from 'vue-property-decorator'

@Component
export default class TimeAgo extends Vue {
  @Prop({ required: true }) timestamp!: string | Date

  timeAgo: string = ''

  private interval: number | null = null

  mounted() {
    this.updateTimeAgo()
    this.interval = window.setInterval(this.updateTimeAgo, 1000) // update every second
  }

  beforeDestroy() {
    if (this.interval) clearInterval(this.interval)
  }

  updateTimeAgo() {
    const now = Date.now()
    const ts = new Date(this.timestamp).getTime()
    const diffSeconds = Math.floor((now - ts) / 1000)

    if (diffSeconds < 60) {
      this.timeAgo = `${diffSeconds} second${diffSeconds !== 1 ? 's' : ''} ago`
    } else if (diffSeconds < 3600) {
      const minutes = Math.floor(diffSeconds / 60)
      this.timeAgo = `${minutes} minute${minutes !== 1 ? 's' : ''} ago`
    } else if (diffSeconds < 86400) {
      const hours = Math.floor(diffSeconds / 3600)
      this.timeAgo = `${hours} hour${hours !== 1 ? 's' : ''} ago`
    } else {
      const days = Math.floor(diffSeconds / 86400)
      this.timeAgo = `${days} day${days !== 1 ? 's' : ''} ago`
    }
  }
}
